<!-- 表单项中的输入框 -->
<template>
  <div class="generalFormTextInput">
    <div
      class="title"
      :style="{ width: selfLabelWidth, minWidth: selfLabelWidth }"
    >
      <label :for="inputId">{{ labelTitle }}</label>
    </div>
    <div class="inputAndIllustrateZone">
      <div class="inputZone" :style="{ width: selfWidth }">
        <div class="inputwrap">
          <input
            :type="inputType"
            :id="inputId"
            :placeholder="placeHolder"
            v-model="inputText"
            @focus="hideErrorMarkWhenFocus"
            @input="checkInputWhenInput"
            @blur="checkInputWhenBlur"
            :style="showErrorColor ? { color: 'red' } : {}"
            :disabled="editDisable"
          />

          <div class="warnwrap">
            <div class="warn" v-show="showErrorColor"></div>
            <div class="warnText" :class="inputId" v-show="showErrorColor">
              <!--                    :style="{ width: warnTextWidth, right: warnTextRight }">-->
              {{ warnText }}
            </div>
          </div>
        </div>
        <slot></slot>
        <!-- <div class="tools">
          <div class="slot">
            
          </div>
        </div> -->
      </div>

      <div class="illustrateZone">
        {{ illustrate }}
      </div>
    </div>
  </div>
</template>

<script>
import { debounce, processStringAsArray } from "../../../../../common/utils";

export default {
  name: "GeneralFormTextInput",
  props: {
    // input[text]的id
    inputId: {
      type: String,
      default: "",
    },
    toUpperCase:{
      default:String,
      default:false
    },
    // 标签text
    labelTitle: {
      type: String,
      default: "",
    },
    // input[text]的提示
    placeHolder: {
      type: String,
      default: "",
    },
    // 填写输入框相关的说明文字
    illustrate: {
      type: String,
      default: "",
    },
    // input[text]的父元素占input[text]的爷爷元素在宽度上的比例
    selfWidth: {
      type: String,
      default: "26%",
    },
    selfLabelWidth: {
      type: String,
      default: "160px",
    },
    illustrationZoneTextMinWidth: {
      type: String,
      default: "0px",
    },
    // input[text]的输入限制类型，每个类型对应有它的判别合规性的方法和错误提示文本
    enterRestrictionType: {
      type: Number,
      //1：非空+非负整数/非负小数(小数点后限2位)
      //2：非空+非负整数
      //3：非空+正整数：正整数【人员配比】
      //4：非空+整数/.5小数【活动天数】
      //5: 非空+1-7之间的整数【活动难易度】
      //6: 非空+1-99之间的整数【最小最大招生年龄】
      //7: 非空+1-20个字符（含1、20）【副宣传语】
      //8: 非空+1-50个字符（含1、30）【产品全称】
      //9：非空+1-20个字符（含1、30）【产品简称】
      //default(include 0): 非空
      default: 0,
    },
    targetDataGenerateType: {
      type: Number,
      default: 1,
    },
    activityStatus: {
      type: String,
      default: "0000",
    },
    inputType: {
      type: String,
      default: "text",
    },
    editDisable: {
      type: Boolean,
      default: false,
    },
  },
  created() {
    // 在组件加载时根据父组件传过来的输入限制类型标记对warnText, warnTextWidth和warnTextRight进行初始化
    // console.log('enter res tupe')
    // console.log(this.enterRestrictionType)
    switch (this.enterRestrictionType) {
      case 1:
        this.warnText = "请输入大于零的数字(小数点后限2位)";
        this.warnTextWidth = "380px";
        this.warnTextRight = "-380px";
        break;
      case 2:
        this.warnText = "不能为空，且只能输入非负整数";
        this.warnTextWidth = "220px";
        this.warnTextRight = "-220px";
        break;
      case 3:
        this.warnText = "不能为空，输入比例，例如1:3或者1：3";
        this.warnTextWidth = "300px";
        this.warnTextRight = "-300px";
        break;
      case 4:
        this.warnText =
          "不能为空，且只能输入1-999之间的整数，若携带小数只能为.5";
        this.warnTextWidth = "400px";
        this.warnTextRight = "-400px";
        break;
      case 5:
        this.warnText = "不能为空，且只能输入1-7之间(含1、7)的整数";
        this.warnTextWidth = "320px";
        this.warnTextRight = "-320px";
        break;
      case 6:
        this.warnText = "不能为空，且只能输入1-99之间(含1、99)的整数";
        this.warnTextWidth = "320px";
        this.warnTextRight = "-320px";
        break;
      case 7:
        this.warnText = "不能为空，且控制在20个字符以内(含20个)";
        this.warnTextWidth = "310px";
        this.warnTextRight = "-310px";
        break;
      case 8:
        this.warnText = "不能为空，且控制在50个字符以内(含50个)";
        this.warnTextWidth = "310px";
        this.warnTextRight = "-310px";
        break;
      case 9:
        this.warnText = "不能为空，且控制在20个字符以内(含20个)";
        this.warnTextWidth = "310px";
        this.warnTextRight = "-310px";
        break;
      case 10:
        this.warnText = "请检查邮件格式";
        this.warnTextWidth = "110px";
        this.warnTextRight = "-130px";
        break;
      case 11:
        this.warnText = "请检查电话格式";
        this.warnTextWidth = "110px";
        this.warnTextRight = "-130px";
        break;
      case 12:
        this.warnText = "纳税人识别号码有误";
        this.warnTextWidth = "160px";
        this.warnTextRight = "-130px";
        break;
      case 13:
        this.warnText = "证件号只能包含大写字母和数字";
        this.warnTextWidth = "200px";
        this.warnTextRight = "-130px";
        break;
      default:
        this.warnText = "不能为空";
        this.warnTextWidth = "90px";
        this.warnTextRight = "-90px";
    }

    function emitTrue() {
      this.$emit("setLegitimacyTrue");
    }

    this.debounceEmitTrue = debounce(emitTrue, 500);

    function emitFalse() {
      this.$emit("setLegitimacyFalse");
    }

    this.debounceEmitFalse = debounce(emitFalse, 500);

    function emitUpdateTrue() {
      this.$emit("setUpdateStatusTrue");
    }

    this.debounceEmitUpdateTrue = debounce(emitUpdateTrue, 500);

    function emitUpdateFalse() {
      this.$emit("setUpdateStatusFalse");
    }

    this.debounceEmitUpdateFalse = debounce(emitUpdateFalse, 500);
  },
  data() {
    return {
      disableMark: false,
      inputText: "",
      // 是否展示错误提示相关样式的标记
      showErrorColor: false,
      // 错误提示文本
      warnText: "",
      // 错误提示框的宽度
      warnTextWidth: null,
      // 错误提示框距relative父元素右侧的距离，一般情况下取warnTextWidth的负值
      warnTextRight: null,

      debounceEmitTrue: null,
      debounceEmitFalse: null,

      elementOffsetTop: 0,

      originalInputText: "",
      componentStatusCode: "100",
      debounceEmitUpdateTrue: null,
      debounceEmitUpdateFalse: null,
      targetData: null,
    };
  },
  methods: {
    // 输入框获得焦点时每按下键盘就会触发该方法的执行
    checkInputWhenInput(e) {
      const text = e.target.value;
      let result;
      const arr = text.split(""); // 字符串转成数组

      switch (this.enterRestrictionType) {
        //1：非空+非负整数/非负小数(小数点后限2位)
        case 1:
          result = /^[0-9]+(\.[0-9]{1,2})?$/.test(text); // 限2位小数
          if (result) {
            if (arr.length >= 2 && arr[0] === "0" && arr[1] !== ".") {
              result = false;
            }
          }
          if (text <= 0) {
            result = false;
          }
          this.showErrorColor = result ? false : true;
          break;
        //2：非空+非负整数
        case 2:
          result = /^[0-9]+$/.test(text);
          if (result) {
            if (arr.length >= 2 && arr[0] === "0") {
              result = false;
            }
          }
          this.showErrorColor = result ? false : true;
          break;
        //3：非空+正整数：正整数【人员配比】
        case 3:
          this.showErrorColor = false;
          break;
        //4：非空+正整数/.5小数【活动天数】
        case 4:
          result = /^[1-9]{1}([0-9]{1,2})?(\.5)?$/.test(text);
          this.showErrorColor = result ? false : true;
          break;
        //5: 非空+1-7之间的整数【活动难易度】
        case 5:
          result = /^[1-7]$/.test(text);
          this.showErrorColor = result ? false : true;
          break;
        //6: 非空+1-99之间的整数【最小最大招生年龄】
        case 6:
          console.log(text, "得到的结果", typeof text);
          result = /^([1-9]\d|\d)$/.test(text);
          if (text == 0) {
            result = false;
          }
          this.showErrorColor = result ? false : true;
          break;
        //7: 非空+20个字符以内(含20个)【副宣传语】
        case 7:
          const len = e.target.value.length;
          this.showErrorColor = len > 20 ? true : false;
          break;
        //default(include 0): 非空
        case 8:
          const len2 = e.target.value.length;
          this.showErrorColor = len2 > 50 ? true : false;
          break;
        case 9:
          const len3 = e.target.value.length;
          this.showErrorColor = len3 > 20 ? true : false;
          break;
        case 10:
          result = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(
            text
          );
          this.showErrorColor = result ? false : true;
          break;
        case 11:
          result =
            /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(
              text
            );
          this.showErrorColor = result ? false : true;
          break;

        // 12纳税人识别号

        case 12:
          result = /[0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}/.test(
            text
          );
          this.showErrorColor = result ? false : true;
          break;
        // 数字和大写字母
        case 13:
          result = /^[0-9A-Z]+$/.test(text);
          this.showErrorColor = result ? false : true;
          console.log(result, text);
          break;
        default:
          result = /\S/.test(text);
          this.showErrorColor = result ? false : true;
      }
    },
    // 输入框失去焦点时触发该方法的执行
    checkInputWhenBlur(e) {
      // console.log(e.target.value);
      // const sendValue = [inputId, e.target.value];
      this.$emit("sendValue", [this.inputId, e.target.value]);
      // 【人员配比】输入框组件中，当输入框失去焦点时进行检查【在输入框输入时不进行实质上的检查，以避免影响用户的输入体验】
      if (this.enterRestrictionType === 3) {
        const result = /^[0-9]+[\:|\：][0-9]+$/.test(e.target.value.trim());
        this.showErrorColor = result ? false : true;
        if (result) {
          this.$emit("setLegitimacyTrue");
        } else {
          this.$emit("setLegitimacyFalse");
        }

        if (this.componentStatusCode === "101") {
          if (e.target.value !== this.originalInputText) {
            this.$emit("setUpdateStatusTrue");
          } else {
            this.$emit("setUpdateStatusFalse");
          }
        }
      }
    },
    // 输入框获得焦点时触发该方法的执行
    hideErrorMarkWhenFocus() {
      // 【人员配比】输入框组件中，当输入框获得焦点时隐藏错误提示
      if (this.enterRestrictionType === 3) {
        this.showErrorColor = false;
      }
    },
    //根据enterRestrictionType来返回true或false
    checkEnterRestriction() {
      if (
        (this.enterRestrictionType === 1 ||
          this.enterRestrictionType === 2 ||
          this.enterRestrictionType === 4 ||
          this.enterRestrictionType === 5 ||
          this.enterRestrictionType === 6) &&
        (this.inputType === "number" || this.inputType === "Number")
      ) {
        return true;
      } else {
        return false;
      }
    },
    //根据enterRestrictionType来进一步限制英文状态下的按键输入，但是不能限制不符合要求的复制粘贴这一种情况
    //可以使用vue的自定义指令来限制不符合要求的复制粘贴
    bindEnterRestriction(e) {
      if (
        this.enterRestrictionType === 1 ||
        this.enterRestrictionType === 2 ||
        this.enterRestrictionType === 4 ||
        this.enterRestrictionType === 5 ||
        this.enterRestrictionType === 6
      ) {
        //只能输入数字和小数点，英文状态下的-号也是被禁掉的
        //注：中文状态下的键盘输入还不能限制住，包括也是能输入中文状态下的-号的
        if (e.keyCode !== 46 && (e.keyCode < 48 || e.keyCode > 57)) {
          e.returnValue = false;
        }
      }

      /* if(this.enterRestrictionType === 3) {
                  //只能输入数字,小数点和英文状态下的:
                  //注：中文状态下的键盘输入还不能限制住
                  if (e.keyCode!==46 && e.keyCode!==58 && (e.keyCode<48 || e.keyCode>57)) {
                    e.returnValue = false;
                  }
                } */
    },
  },
  watch: {
    inputText(newVal) {
      if (this.inputText === null || this.inputText === undefined) {
        this.inputText = "";
        return;
      }
      if(this.toUpperCase) this.inputText = this.inputText.toUpperCase()
      if (this.enterRestrictionType !== 3) {
        if (newVal !== "" && !this.showErrorColor) {
          this.debounceEmitTrue();
        } else {
          this.debounceEmitFalse();
        }

        if (this.componentStatusCode === "101") {
          if (typeof newVal === "string") {
            if (newVal.trim() !== this.originalInputText) {
              this.debounceEmitUpdateTrue();
            } else {
              this.debounceEmitUpdateFalse();
            }
          } else {
            if (newVal !== this.originalInputText) {
              this.debounceEmitUpdateTrue();
            } else {
              this.debounceEmitUpdateFalse();
            }
          }
        }
      }
      // 要填人员比例的input
      else {
        const result = /^[0-9]+[\:|\：][0-9]+$/.test(newVal);
        if (result) {
          this.$emit("setLegitimacyTrue");
        } else {
          this.$emit("setLegitimacyFalse");
        }
      }

      if (this.componentStatusCode === "100") {
        if (
          (typeof newVal === "string" && newVal.trim() === "") ||
          typeof newVal === null
        ) {
          this.$emit("setNoEmptyFalse");
        } else {
          this.$emit("setNoEmptyTrue");
        }
      }

      switch (this.targetDataGenerateType) {
        case 2:
          if (newVal !== "") {
            this.targetData = Number(newVal);
          } else {
            this.targetData = null;
          }
          break;
        case 3:
          if (newVal !== "") {
            this.targetData = [newVal.trim()];
          } else {
            this.targetData = null;
          }
          break;
        default:
          if (newVal !== "") {
            this.targetData = newVal.trim();
          } else {
            this.targetData = null;
          }
      }

      this.$bus.$emit("updateDataInCookie");
    },
  },
};
</script>

